import { request } from 'utils';

const prefix = '/api/categories';

export const getStudentTitles = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/StudentTitle',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createStudentTitles = ({ label }) =>
  request({
    prefix,
    url: '/StudentTitle',
    data: { label },
    method: 'POST',
  });

export const deleteStudentTitles = id =>
  request({
    prefix,
    url: `/StudentTitle/${id}`,
    method: 'DELETE',
  });

export const updateStudentTitles = ({ id, label }) =>
  request({
    prefix,
    url: `/StudentTitle/${id}`,
    data: { id, label },
    method: 'PUT',
  });

export const getStudentTitlesById = id =>
  request({
    prefix,
    url: `/StudentTitle/${id}`,
    method: 'GET',
  });



